import { useCallback, useEffect, useRef, useState } from 'react';
import {
  createUserWithEmailAndPassword,
  deleteUser,
  fetchSignInMethodsForEmail,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signInWithPopup,
  signOut,
  updateProfile
} from 'firebase/auth';
import {
  addDoc,
  collection,
  deleteDoc,
  doc,
  getDoc,
  onSnapshot,
  setDoc
} from 'firebase/firestore';
import { ADMIN_ACCOUNT, OWNER_EMAIL, createUserProfile, sanitizeUser } from '../data/constants';
import { auth, db, googleProvider, hasFirebaseConfig } from '../lib/firebase';

const USERS_COLLECTION = 'users';
const PRESENCE_COLLECTION = 'presence';
const USER_ACTIVITY_COLLECTION = 'userActivity';

const PRESENCE_HEARTBEAT_MS = 12000;
const PRESENCE_TTL_MS = 45000;

const EVENT_STATUS_ONLINE = 'status_online';
const EVENT_STATUS_OFFLINE = 'status_offline';

const EVENT_POINTS = 'points';
const EVENT_BOARDS_ADD = 'boards_add';
const EVENT_BOARDS_DEL = 'boards_del';
const EVENT_TASKS_ADD = 'tasks_add';
const EVENT_TASKS_DEL = 'tasks_del';
const EVENT_PURCHASES_ADD = 'purchases_add';
const EVENT_LEDGER_TOPUP = 'ledger_topup';
const EVENT_LEDGER_PURCHASE = 'ledger_purchase';

const toIsoNow = () => new Date().toISOString();
const toIsoFuture = (ms) => new Date(Date.now() + ms).toISOString();

const normalizeId = (value) => String(value ?? '').trim();

const idsSetFrom = (items) =>
  new Set(
    (Array.isArray(items) ? items : [])
      .map((item) => normalizeId(item?.id))
      .filter(Boolean)
  );

const collectAddedIds = (beforeIds, afterIds) =>
  [...afterIds].filter((id) => !beforeIds.has(id));

const collectDeletedIds = (beforeIds, afterIds) =>
  [...beforeIds].filter((id) => !afterIds.has(id));

const summarizeEvents = (beforeUser, afterUser) => {
  if (!beforeUser || !afterUser) {
    return [];
  }

  const events = [];

  const beforePoints = Number(beforeUser.points || 0);
  const afterPoints = Number(afterUser.points || 0);
  if (beforePoints !== afterPoints) {
    events.push({
      type: EVENT_POINTS,
      data: {
        d: afterPoints - beforePoints,
        n: afterPoints
      }
    });
  }

  const beforeBoards = idsSetFrom(beforeUser.boards);
  const afterBoards = idsSetFrom(afterUser.boards);
  const addedBoards = collectAddedIds(beforeBoards, afterBoards);
  const deletedBoards = collectDeletedIds(beforeBoards, afterBoards);
  if (addedBoards.length > 0) {
    events.push({ type: EVENT_BOARDS_ADD, data: { c: addedBoards.length } });
  }
  if (deletedBoards.length > 0) {
    events.push({ type: EVENT_BOARDS_DEL, data: { c: deletedBoards.length } });
  }

  const beforeTasks = idsSetFrom(beforeUser.tasks);
  const afterTasks = idsSetFrom(afterUser.tasks);
  const addedTasks = collectAddedIds(beforeTasks, afterTasks);
  const deletedTasks = collectDeletedIds(beforeTasks, afterTasks);
  if (addedTasks.length > 0) {
    events.push({ type: EVENT_TASKS_ADD, data: { c: addedTasks.length } });
  }
  if (deletedTasks.length > 0) {
    events.push({ type: EVENT_TASKS_DEL, data: { c: deletedTasks.length } });
  }

  const beforePurchases = idsSetFrom(beforeUser.purchases);
  const afterPurchases = idsSetFrom(afterUser.purchases);
  const addedPurchases = collectAddedIds(beforePurchases, afterPurchases);
  if (addedPurchases.length > 0) {
    events.push({ type: EVENT_PURCHASES_ADD, data: { c: addedPurchases.length } });
  }

  const beforeLedgerIds = idsSetFrom(beforeUser.pointLedger);
  const newLedgerEntries = (Array.isArray(afterUser.pointLedger) ? afterUser.pointLedger : [])
    .filter((entry) => !beforeLedgerIds.has(normalizeId(entry?.id)));

  if (newLedgerEntries.length > 0) {
    let topupCount = 0;
    let topupAmount = 0;
    let purchaseCount = 0;
    let purchaseAmount = 0;

    newLedgerEntries.forEach((entry) => {
      const source = String(entry?.source || '').toLowerCase();
      const amount = Math.abs(Number(entry?.amount || 0));

      if (source.includes('purchase')) {
        purchaseCount += 1;
        purchaseAmount += amount;
      }

      if (
        source.includes('topup')
        || source.includes('deposit')
        || source.includes('pack')
      ) {
        topupCount += 1;
        topupAmount += amount;
      }
    });

    if (topupCount > 0) {
      events.push({
        type: EVENT_LEDGER_TOPUP,
        data: { c: topupCount, a: Math.round(topupAmount) }
      });
    }

    if (purchaseCount > 0) {
      events.push({
        type: EVENT_LEDGER_PURCHASE,
        data: { c: purchaseCount, a: Math.round(purchaseAmount) }
      });
    }
  }

  return events;
};

const isPresenceOnline = (entry, now = Date.now()) => {
  if (!entry?.isOnline) {
    return false;
  }

  const expiresAt = Date.parse(entry.expiresAt || '');
  if (Number.isFinite(expiresAt)) {
    return expiresAt > now;
  }

  const lastSeenAt = Date.parse(entry.lastSeenAt || '');
  return Number.isFinite(lastSeenAt) && now - lastSeenAt <= PRESENCE_TTL_MS;
};

const enrichUserWithPresence = (user, presenceEntry, now = Date.now()) => {
  if (!presenceEntry) {
    return {
      ...user,
      isOnline: false
    };
  }

  return {
    ...user,
    isOnline: isPresenceOnline(presenceEntry, now),
    lastSeenAt: presenceEntry.lastSeenAt || user.lastSeenAt || null,
    lastLoginAt: presenceEntry.lastLoginAt || user.lastLoginAt || null,
    lastLogoutAt: presenceEntry.lastLogoutAt || user.lastLogoutAt || null
  };
};

const createDefaultAuthForm = () => ({
  identifier: '',
  password: '',
  name: '',
  email: '',
  phone: ''
});

const mapAuthError = (error) => {
  switch (error?.code) {
    case 'auth/invalid-credential':
      return ' email  .';
    case 'auth/user-not-found':
      return '   email  .';
    case 'auth/wrong-password':
      return ' .';
    case 'auth/email-already-in-use':
      return ' email  .';
    case 'auth/invalid-email':
      return '  email.';
    case 'auth/weak-password':
      return '    6 .';
    case 'auth/operation-not-allowed':
      return ' Firebase     .';
    case 'auth/unauthorized-domain':
      return '     Authorized domains  Firebase Auth.';
    case 'auth/popup-blocked':
      return '   .  pop-up    .';
    case 'auth/popup-closed-by-user':
      return '   Google .';
    case 'auth/cancelled-popup-request':
      return '   Google .';
    case 'auth/account-exists-with-different-credential':
      return '  email     .    \' Google.';
    case 'auth/network-request-failed':
      return '   .';
    case 'auth/user-disabled':
      return '  .';
    default:
      return '   .   .';
  }
};

const formatBanUntil = (value) => {
  if (!value) {
    return '';
  }

  const parsed = new Date(value);
  if (Number.isNaN(parsed.getTime())) {
    return '';
  }

  return parsed.toLocaleString('uk-UA', {
    day: '2-digit',
    month: '2-digit',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
};

const isBanActive = (profile) => {
  if (!profile?.isBanned || profile?.isAdmin) {
    return false;
  }

  if (!profile?.banExpiresAt) {
    return true;
  }

  const banExpires = new Date(profile.banExpiresAt).getTime();
  if (Number.isNaN(banExpires)) {
    return true;
  }

  return banExpires > Date.now();
};

const buildBanMessage = (profile) => {
  const reason = profile?.banReason?.trim() || '  .';
  const details = profile?.banDescription?.trim();
  const until = formatBanUntil(profile?.banExpiresAt);

  if (details) {
    return `${reason} : ${details}. : ${until}.`;
  }

  return `${reason} : ${until}.`;
};

export default function useAuthStore(showAlert) {
  const [authMode, setAuthModeState] = useState('login');
  const [authBusy, setAuthBusy] = useState(false);
  const [authError, setAuthError] = useState('');
  const [authForm, setAuthForm] = useState(createDefaultAuthForm);

  const [isAuthReady, setAuthReady] = useState(!hasFirebaseConfig);
  const [isProfileReady, setProfileReady] = useState(!hasFirebaseConfig);

  const [sessionUser, setSessionUser] = useState(null);
  const [currentUser, setCurrentUser] = useState(null);
  const [allUsers, setAllUsers] = useState([]);
  const [liveUsersCount, setLiveUsersCount] = useState(0);

  const presenceMapRef = useRef(new Map());
  const presenceIdentityRef = useRef({
    name: '',
    avatar: '',
    email: ''
  });

  const canAccessUsersDirectory = Boolean(
    currentUser?.isAdmin
      || currentUser?.roles?.includes('operator')
      || currentUser?.roles?.includes('task_moderator')
  );

  const setAuthMode = useCallback((mode) => {
    setAuthModeState(mode);
    setAuthError('');
  }, []);

  const onAuthFormChange = useCallback((field, value) => {
    setAuthForm((previous) => ({
      ...previous,
      [field]: value
    }));
  }, []);

  useEffect(() => {
    presenceIdentityRef.current = {
      name: currentUser?.name || sessionUser?.displayName || '',
      avatar: currentUser?.avatar || sessionUser?.photoURL || '',
      email: (currentUser?.email || sessionUser?.email || '').trim().toLowerCase()
    };
  }, [
    currentUser?.avatar,
    currentUser?.email,
    currentUser?.name,
    sessionUser?.displayName,
    sessionUser?.email,
    sessionUser?.photoURL
  ]);

  useEffect(() => {
    if (!hasFirebaseConfig || !auth) {
      return;
    }

    const unsubscribe = onAuthStateChanged(auth, (nextUser) => {
      setSessionUser(nextUser);
      setAuthReady(true);
      if (nextUser) {
        setAuthError('');
      }
      setProfileReady(false);

      if (!nextUser) {
        setCurrentUser(null);
        setAllUsers([]);
        setLiveUsersCount(0);
        presenceMapRef.current = new Map();
      }
    });

    return unsubscribe;
  }, []);

  useEffect(() => {
    if (!hasFirebaseConfig || !sessionUser || !db) {
      if (!sessionUser) {
        setProfileReady(true);
      }
      return;
    }

    let cancelled = false;
    let unsubscribeProfile = null;

    const syncProfile = async () => {
      const userRef = doc(db, USERS_COLLECTION, sessionUser.uid);
      const normalizedEmail = (sessionUser.email || '').trim().toLowerCase();
      const isOwner = normalizedEmail === OWNER_EMAIL && Boolean(sessionUser.emailVerified);

      try {
        const existingSnapshot = await getDoc(userRef);
        let bootstrapProfile = null;

        if (!existingSnapshot.exists()) {
          if (!normalizedEmail) {
            showAlert('danger', '   email .');
            await signOut(auth);
            return;
          }

          const created = createUserProfile({
            id: sessionUser.uid,
            name: sessionUser.displayName || (isOwner ? ADMIN_ACCOUNT.name : ''),
            email: normalizedEmail,
            phone: '',
            avatar: sessionUser.photoURL || '',
            isAdmin: isOwner
          });

          await setDoc(userRef, created, { merge: false });
          bootstrapProfile = created;
        } else {
          const existing = existingSnapshot.data() || {};
          const patch = {};

          if (!existing.id) {
            patch.id = sessionUser.uid;
          }
          if (!existing.email && normalizedEmail) {
            patch.email = normalizedEmail;
          }
          if (!existing.name && sessionUser.displayName) {
            patch.name = sessionUser.displayName;
          }
          if (!existing.avatar && sessionUser.photoURL) {
            patch.avatar = sessionUser.photoURL;
          }
          if (isOwner && !existing.isAdmin) {
            patch.isAdmin = true;
          }

          if (Object.keys(patch).length > 0) {
            await setDoc(userRef, patch, { merge: true });
          }

          bootstrapProfile = { ...existing, ...patch, id: sessionUser.uid };
        }

        if (cancelled || !bootstrapProfile) {
          return;
        }

        const normalizedBootstrap = sanitizeUser({
          ...bootstrapProfile,
          id: sessionUser.uid,
          email: bootstrapProfile.email || normalizedEmail,
          isAdmin: isOwner
        });

        if (normalizedBootstrap.isBanned && !normalizedBootstrap.isAdmin && isBanActive(normalizedBootstrap)) {
          const banMessage = buildBanMessage(normalizedBootstrap);
          setCurrentUser(null);
          setProfileReady(true);
          setAuthError(banMessage);
          showAlert('danger', banMessage);
          await signOut(auth);
          return;
        }

        setCurrentUser(normalizedBootstrap);
        setProfileReady(true);

        unsubscribeProfile = onSnapshot(
          userRef,
          async (snapshot) => {
            if (!snapshot.exists()) {
              setCurrentUser(null);
              setProfileReady(true);
              return;
            }

            const normalized = sanitizeUser({
              ...snapshot.data(),
              id: sessionUser.uid,
              email: snapshot.data()?.email || normalizedEmail,
              isAdmin: isOwner
            });

            if (normalized.isBanned && !normalized.isAdmin && isBanActive(normalized)) {
              const banMessage = buildBanMessage(normalized);
              setCurrentUser(null);
              setProfileReady(true);
              setAuthError(banMessage);
              showAlert('danger', banMessage);
              await signOut(auth);
              return;
            }

            setCurrentUser(normalized);
            setProfileReady(true);
          },
          async () => {
            setCurrentUser(null);
            setProfileReady(true);
            showAlert('danger', '     Firestore.   .');
            await signOut(auth);
          }
        );
      } catch (error) {
        setCurrentUser(null);
        setProfileReady(true);
        showAlert('danger', '      .');
        await signOut(auth);
      }
    };

    syncProfile();

    return () => {
      cancelled = true;
      if (unsubscribeProfile) {
        unsubscribeProfile();
      }
    };
  }, [sessionUser, showAlert]);

  useEffect(() => {
    if (!sessionUser || !canAccessUsersDirectory || !db) {
      setAllUsers([]);
      return;
    }

    const usersRef = collection(db, USERS_COLLECTION);

    const unsubscribe = onSnapshot(
      usersRef,
      (snapshot) => {
        const now = Date.now();
        const users = snapshot.docs
          .map((snapshotDoc) =>
            sanitizeUser({
              ...snapshotDoc.data(),
              id: snapshotDoc.id
            })
          )
          .sort((left, right) => {
            if (left.isAdmin !== right.isAdmin) {
              return Number(right.isAdmin) - Number(left.isAdmin);
            }
            return new Date(right.joinedAt).getTime() - new Date(left.joinedAt).getTime();
          })
          .map((user) => enrichUserWithPresence(user, presenceMapRef.current.get(user.id), now));

        setAllUsers(users);
      },
      () => {
        setAllUsers([]);
        showAlert('warning', '      .');
      }
    );

    return unsubscribe;
  }, [sessionUser, canAccessUsersDirectory, showAlert]);

  useEffect(() => {
    if (!sessionUser?.uid || !db) {
      setLiveUsersCount(0);
      presenceMapRef.current = new Map();
      return;
    }

    const calculateFromMap = (sourceMap = presenceMapRef.current) => {
      const now = Date.now();
      let onlineCount = 0;
      sourceMap.forEach((entry) => {
        if (isPresenceOnline(entry, now)) {
          onlineCount += 1;
        }
      });
      setLiveUsersCount(onlineCount);

      if (canAccessUsersDirectory) {
        setAllUsers((previous) =>
          previous.map((user) => enrichUserWithPresence(user, sourceMap.get(user.id), now))
        );
      }
    };

    const presenceRef = collection(db, PRESENCE_COLLECTION);
    const unsubscribe = onSnapshot(
      presenceRef,
      (snapshot) => {
        const nextMap = new Map();
        snapshot.docs.forEach((snapshotDoc) => {
          const payload = snapshotDoc.data() || {};
          const uid = payload.uid || snapshotDoc.id;
          nextMap.set(uid, { ...payload, uid });
        });

        presenceMapRef.current = nextMap;
        calculateFromMap(nextMap);
      },
      () => {
        setLiveUsersCount(0);
      }
    );

    const recalcInterval = setInterval(() => {
      calculateFromMap();
    }, 4000);

    return () => {
      clearInterval(recalcInterval);
      unsubscribe();
    };
  }, [sessionUser?.uid, canAccessUsersDirectory]);

  useEffect(() => {
    if (!sessionUser?.uid || !db) {
      return;
    }

    const uid = sessionUser.uid;
    const sessionId = `${uid}-${Date.now().toString(36)}-${Math.random().toString(36).slice(2, 8)}`;
    const presenceDocRef = doc(db, PRESENCE_COLLECTION, uid);
    const activityRef = collection(db, USER_ACTIVITY_COLLECTION);
    let stopped = false;
    let offlineSent = false;

    const writePresence = async ({ isOnline, eventType = '' } = {}) => {
      if (stopped && isOnline) {
        return;
      }

      const nowIso = toIsoNow();
      const identity = presenceIdentityRef.current;
      const payload = {
        uid,
        name: identity.name || '',
        avatar: identity.avatar || '',
        email: identity.email || '',
        isOnline: Boolean(isOnline),
        lastSeenAt: nowIso,
        expiresAt: isOnline ? toIsoFuture(PRESENCE_TTL_MS) : nowIso,
        sessionId
      };

      if (eventType === EVENT_STATUS_ONLINE) {
        payload.lastLoginAt = nowIso;
      }
      if (eventType === EVENT_STATUS_OFFLINE) {
        payload.lastLogoutAt = nowIso;
      }

      try {
        await setDoc(presenceDocRef, payload, { merge: true });
      } catch {
        // ignore presence write errors
      }

      if (!eventType) {
        return;
      }

      try {
        await addDoc(activityRef, {
          uid,
          type: eventType,
          createdAt: nowIso
        });
      } catch {
        // ignore activity write errors
      }
    };

    const publishOffline = () => {
      if (offlineSent) {
        return;
      }
      offlineSent = true;
      void writePresence({ isOnline: false, eventType: EVENT_STATUS_OFFLINE });
    };

    const pingInterval = setInterval(() => {
      void writePresence({ isOnline: true });
    }, PRESENCE_HEARTBEAT_MS);

    void writePresence({ isOnline: true, eventType: EVENT_STATUS_ONLINE });

    window.addEventListener('beforeunload', publishOffline);
    window.addEventListener('pagehide', publishOffline);

    return () => {
      stopped = true;
      clearInterval(pingInterval);
      window.removeEventListener('beforeunload', publishOffline);
      window.removeEventListener('pagehide', publishOffline);
      publishOffline();
    };
  }, [sessionUser?.uid]);

  const persistUser = useCallback(
    async (userId, candidate, options = {}) => {
      if (!db) {
        return false;
      }

      const normalized = sanitizeUser({ ...candidate, id: userId });
      let previousUser = null;

      try {
        const snapshot = await getDoc(doc(db, USERS_COLLECTION, userId));
        if (snapshot.exists()) {
          previousUser = sanitizeUser({
            ...snapshot.data(),
            id: userId
          });
        }
      } catch {
        // ignore read error and still try to save
      }

      if (options.lockIdentity && currentUser) {
        normalized.email = currentUser.email;
        normalized.isAdmin = currentUser.isAdmin;
        normalized.roles = currentUser.roles;
        normalized.flags = currentUser.flags;
        normalized.isBanned = currentUser.isBanned;
        normalized.banReason = currentUser.banReason;
        normalized.banDescription = currentUser.banDescription;
        normalized.banExpiresAt = currentUser.banExpiresAt;
        normalized.bannedAt = currentUser.bannedAt;
      }

      try {
        await setDoc(doc(db, USERS_COLLECTION, userId), normalized, { merge: false });

        if (previousUser) {
          const events = summarizeEvents(previousUser, normalized);
          if (events.length > 0) {
            const actorUid = currentUser?.id || sessionUser?.uid || userId;
            await Promise.all(
              events.map((entry) =>
                addDoc(collection(db, USER_ACTIVITY_COLLECTION), {
                  uid: userId,
                  type: entry.type,
                  createdAt: toIsoNow(),
                  actorUid,
                  ...entry.data
                })
              )
            ).catch(() => {
              // do not fail user save if activity log write failed
            });
          }
        }

        return true;
      } catch {
        showAlert('danger', options.errorMessage || '    .');
        return false;
      }
    },
    [currentUser, sessionUser?.uid, showAlert]
  );

  const persistCurrentUser = useCallback(
    async (candidate, successMessage) => {
      if (!currentUser) {
        return false;
      }

      const saved = await persistUser(currentUser.id, candidate, {
        lockIdentity: !currentUser.isAdmin,
        errorMessage: '       .'
      });

      if (saved && successMessage) {
        showAlert('success', successMessage);
      }

      return saved;
    },
    [currentUser, persistUser, showAlert]
  );

  const onRegister = useCallback(
    async (event) => {
      event.preventDefault();

      if (!auth || !db) {
        return;
      }

      const name = authForm.name.trim();
      const email = authForm.email.trim().toLowerCase();
      const phone = authForm.phone.trim();
      const password = authForm.password;

      if (!name) {
        setAuthError(' \'  .');
        return;
      }
      if (!email) {
        setAuthError(' email  .');
        return;
      }
      if (password.length < 6) {
        setAuthError('    6 .');
        return;
      }

      setAuthBusy(true);
      setAuthError('');

      try {
        const methods = await fetchSignInMethodsForEmail(auth, email);
        if (methods.length > 0) {
          try {
            const credentials = await signInWithEmailAndPassword(auth, email, password);
            const snapshot = await getDoc(doc(db, USERS_COLLECTION, credentials.user.uid));
            if (snapshot.exists()) {
              const profile = sanitizeUser({ ...snapshot.data(), id: credentials.user.uid });
              if (isBanActive(profile)) {
                const banMessage = buildBanMessage(profile);
                setAuthError(banMessage);
                await signOut(auth);
                return;
              }
            }
            await signOut(auth);
          } catch {
            //   ,   .
          }
        }

        const credentials = await createUserWithEmailAndPassword(auth, email, password);
        await updateProfile(credentials.user, { displayName: name });

        const profile = createUserProfile({
          id: credentials.user.uid,
          name,
          email,
          phone,
          avatar: credentials.user.photoURL || '',
          isAdmin: email === OWNER_EMAIL && Boolean(credentials.user.emailVerified)
        });

        await setDoc(doc(db, USERS_COLLECTION, credentials.user.uid), profile, { merge: false });

        setAuthForm(createDefaultAuthForm());
        setAuthModeState('login');
        showAlert('success', '  .');
      } catch (error) {
        setAuthError(mapAuthError(error));
      } finally {
        setAuthBusy(false);
      }
    },
    [authForm, showAlert]
  );

  const onLogin = useCallback(
    async (event) => {
      event.preventDefault();

      if (!auth || !db) {
        return;
      }

      const email = authForm.identifier.trim().toLowerCase();
      const password = authForm.password;

      if (!email || !password) {
        setAuthError(' email    .');
        return;
      }

      setAuthBusy(true);
      setAuthError('');

      try {
        const credentials = await signInWithEmailAndPassword(auth, email, password);
        const snapshot = await getDoc(doc(db, USERS_COLLECTION, credentials.user.uid));
        if (snapshot.exists()) {
          const profile = sanitizeUser({ ...snapshot.data(), id: credentials.user.uid });
          if (isBanActive(profile)) {
            const banMessage = buildBanMessage(profile);
            setAuthError(banMessage);
            await signOut(auth);
            return;
          }
        }
        setAuthForm(createDefaultAuthForm());
      } catch (error) {
        setAuthError(mapAuthError(error));
      } finally {
        setAuthBusy(false);
      }
    },
    [authForm, db]
  );

  const onGoogleSignIn = useCallback(async () => {
    if (!auth) {
      return;
    }

    setAuthBusy(true);
    setAuthError('');

    try {
      await signInWithPopup(auth, googleProvider);
      setAuthForm(createDefaultAuthForm());
    } catch (error) {
      setAuthError(mapAuthError(error));
    } finally {
      setAuthBusy(false);
    }
  }, []);

  const onLogout = useCallback(async () => {
    if (!auth) {
      return;
    }

    try {
      await signOut(auth);
      showAlert('success', '   .');
    } catch {
      showAlert('danger', '   .');
    }
  }, [showAlert]);

  const onDeleteAccount = useCallback(async () => {
    if (!auth || !db || !auth.currentUser || !currentUser) {
      return false;
    }

    if (currentUser.isAdmin) {
      showAlert('warning', '   owner-  .');
      return false;
    }

    const confirmed = window.confirm(' ?    .');
    if (!confirmed) {
      return false;
    }

    try {
      await deleteDoc(doc(db, USERS_COLLECTION, currentUser.id));
      await deleteDoc(doc(db, PRESENCE_COLLECTION, currentUser.id));
    } catch {
      showAlert('danger', '      .');
      return false;
    }

    try {
      await deleteUser(auth.currentUser);
      showAlert('success', ' .');
      return true;
    } catch (error) {
      if (error?.code === 'auth/requires-recent-login') {
        showAlert(
          'warning',
          ' ,      Firebase   .'
        );
      } else {
        showAlert('warning', ' ,        Firebase Auth.');
      }

      try {
        await signOut(auth);
      } catch {
        // ignore
      }
      return false;
    }
  }, [currentUser, showAlert]);

  return {
    authMode,
    authBusy,
    authError,
    authForm,
    isAuthReady,
    isProfileReady,
    sessionUser,
    currentUser,
    allUsers,
    liveUsersCount,
    setAuthMode,
    onAuthFormChange,
    onLogin,
    onRegister,
    onGoogleSignIn,
    onLogout,
    onDeleteAccount,
    persistUser,
    persistCurrentUser
  };
}
